







import Vue from 'vue';

import SignUp from '@/components/SignUp.vue';
import Confirmation from '@/components/Confirmation.vue';

export default Vue.extend({
  name: 'Auth',
  components: {
    SignUp,
    Confirmation
  },
  data() {
    return {
      isSignedUp: false
    };
  },
  methods: {
    awaitVerify(): void {
      this.isSignedUp = true;
    }
  }
});
